<template>
  <nut-noticebar :text="text" />
  <nut-row>
    <nut-col :span="10">
      <nut-input v-model="val1"
                 placeholder="开始日期"
                 readonly
                 @click-input="click1" />
      <nut-popup v-model:visible="show1"
                 position="bottom">
        <nut-date-picker v-model="valDate1"
                         :min-date="min"
                         :max-date="max"
                         :three-dimensional="false"
                         @confirm="confirm1"></nut-date-picker>
      </nut-popup>
    </nut-col>
    <nut-col :span="10">
      <nut-input v-model="val2"
                 placeholder="结束日期"
                 readonly
                 @click-input="click2" />
      <nut-popup v-model:visible="show2"
                 position="bottom">
        <nut-date-picker v-model="valDate2"
                         :min-date="min"
                         :max-date="max"
                         :three-dimensional="false"
                         @confirm="confirm2"></nut-date-picker>
      </nut-popup>
    </nut-col>
    <nut-col :span="4">
      <nut-button :loading="isLoading"
                  type="info"
                  @click="onChange">查询</nut-button>
    </nut-col>
  </nut-row>
  <nut-infinite-loading v-model="infinityValue"
                        :has-more="hasMore"
                        @load-more="loadMore">
    <nut-pull-refresh v-model="refresh"
                      loosing-txt="松开吧"
                      loading-txt="玩命加载中..."
                      :complete-duration="1000"
                      @refresh="refreshFun">
      <template #pulling-txt>
        <div>用力拉</div>
      </template>
      <nut-collapse v-for="(match, index) in matchResult"
                    :key="index"
                    @change="change">
        <nut-collapse-item :name="index"
                           :title="`${match.matchDate}  ${match.matchNumStr}   ${match.leagueNameAbbr}`">
          <template #extra><span class="danguan"
                  v-if="match.bettingSingle ==1">({{ match.goalLine }}){{ match.homeTeam }} VS {{ match.awayTeam }}</span>
            <span style=" margin-right: 20px;"
                  v-else>({{ match.goalLine }}){{ match.homeTeam }} VS {{ match.awayTeam }}</span><nut-tag plain
                     type="primary"> {{match.sectionsNo999}} </nut-tag> </template>
          <nut-row v-for="(change, indexChange) in match.changeList"
                   :key="indexChange">
            <template v-if="change.col == 1">
              <nut-col :span="6">
                <div class="left">{{change.name}}</div>
              </nut-col>
              <nut-col :span="6">
                <div class="content"><span class="numSpan">{{change.value1}}</span>
                  <template v-if="change.type == 1 && change.value1 > 0">
                    <TriangleUp color="rgb(250, 44, 25)"
                                class="icon" />
                  </template>
                  <template v-else-if="change.type == 1 && change.value1 < 0">
                    <TriangleDown color="rgb(100, 181, 120)"
                                  class="icon" />
                  </template>
                  <template v-else-if="change.type == 2 && change.num1 <=-10">
                    <TriangleDown color="rgb(128 206 245)"
                                  class="icon" />
                  </template>
                  <template v-else-if="change.type == 2 && change.num1 <=-20">
                    <TriangleDown color="rgb(11 26 219)"
                                  class="icon" />
                  </template>
                  <template v-else-if="change.type == 2 && change.num1 >= 10">
                    <TriangleUp color="rgb(243 134 122)"
                                class="icon" />
                  </template>
                  <template v-else-if="change.type == 2 && change.num1 >= 20">
                    <TriangleUp color="rgb(243 26 2)"
                                class="icon" />
                  </template>
                </div>
              </nut-col>
              <nut-col :span="6">
                <div class="content"><span class="numSpan">{{change.value2}}</span>
                  <template v-if="change.type == 1 && change.value2 > 0">
                    <TriangleUp color="rgb(250, 44, 25)"
                                class="icon" />
                  </template>
                  <template v-else-if="change.type == 1 && change.value2 < 0">
                    <TriangleDown color="rgb(100, 181, 120)"
                                  class="icon" />
                  </template>
                  <template v-else-if="change.type == 2 && change.num2 <=-10">
                    <TriangleDown color="rgb(128 206 245)"
                                  class="icon" />
                  </template>
                  <template v-else-if="change.type == 2 && change.num2 <=-20">
                    <TriangleDown color="rgb(11 26 219)"
                                  class="icon" />
                  </template>
                  <template v-else-if="change.type == 2 && change.num2 >= 10">
                    <TriangleUp color="rgb(243 134 122)"
                                class="icon" />
                  </template>
                  <template v-else-if="change.type == 2 && change.num2 >= 20">
                    <TriangleUp color="rgb(243 26 2)"
                                class="icon" />
                  </template>
                </div>
              </nut-col>
              <nut-col :span="6">
                <div class="content"><span class="numSpan">{{change.value3}}</span>
                  <template v-if="change.type == 1 && change.value3 > 0">
                    <TriangleUp color="rgb(250, 44, 25)"
                                class="icon" />
                  </template>
                  <template v-else-if="change.type == 1 && change.value3 < 0">
                    <TriangleDown color="rgb(100, 181, 120)"
                                  class="icon" />
                  </template>
                  <template v-else-if="change.type == 2 && change.num3 <=-10">
                    <TriangleDown color="rgb(128 206 245)"
                                  class="icon" />
                  </template>
                  <template v-else-if="change.type == 2 && change.num3 <=-20">
                    <TriangleDown color="rgb(11 26 219)"
                                  class="icon" />
                  </template>
                  <template v-else-if="change.type == 2 && change.num3 >= 10">
                    <TriangleUp color="rgb(243 134 122)"
                                class="icon" />
                  </template>
                  <template v-else-if="change.type == 2 && change.num3 >= 20">
                    <TriangleUp color="rgb(243 26 2)"
                                class="icon" />
                  </template>
                </div>
              </nut-col>
            </template>
            <template v-else-if="change.col == 3">
              <nut-col :span="6">
                <div class="left">{{change.name}}</div>
              </nut-col>
              <nut-col :span="18">
                <div class="content">{{change.value1}}</div>
              </nut-col>
            </template>
          </nut-row>
        </nut-collapse-item>
      </nut-collapse>
    </nut-pull-refresh>
  </nut-infinite-loading>
</template>

<script setup>
import { onMounted, ref } from 'vue'
import { TriangleUp, TriangleDown } from '@nutui/icons-vue'
import axios from 'axios'
import { showNotify } from '@nutui/nutui'
const timeNotify = (msg) => {
  showNotify.warn(msg, { duration: 2000 })
}
const matchResult = ref([]);
const refresh = ref(true)
const text = ref(
  '当前页面为体彩足球赛果开奖预览。'
)
const refreshFun = () => {
  cycle.value = 0;
  val1.value = formattedThreeDaysAgoString();
  valDate1.value = formattedThreeDaysAgo();
  val2.value = formattedCurrentDateString();
  valDate2.value = formattedCurrentDate();
  getMatchResultV1();
  refresh.value = false
  hasMore.value = true
}

const cycle = ref(0)
const tabsValue = ref(0)
const infinityValue = ref(false)
const hasMore = ref(true)
const loadMore = (done) => {
  getMatchResultV1(cycle.value);
  if (cycle.value > tabsValue.value) hasMore.value = false
  infinityValue.value = false
}

const val1 = ref('')
const valDate1 = ref(new Date())
const val2 = ref('')
const valDate2 = ref(new Date())
const show1 = ref(false)
const show2 = ref(false)
const min = new Date(2020, 0, 1)
const max = new Date()
const click1 = () => {
  console.log('click-input')
  show1.value = true
}
const click2 = () => {
  console.log('click-input')
  show2.value = true
}

const confirm1 = ({ selectedValue }) => {
  console.log(selectedValue)
  val1.value = `${selectedValue[0]}-${selectedValue[1]}-${selectedValue[2]}`
  show1.value = false
}

const confirm2 = ({ selectedValue }) => {
  console.log(selectedValue)
  val2.value = `${selectedValue[0]}-${selectedValue[1]}-${selectedValue[2]}`
  show2.value = false
}

const isLoading = ref(false)
const onChange = () => {
  isLoading.value = true
  cycle.value = 0;
  hasMore.value = true
  getMatchResultV1();
  isLoading.value = false
}


const change = (modelValue, name, status) => {
  if (status) {
    let matchResultValue = matchResult.value;
    console.log(matchResultValue[name].changeList, matchResultValue.changeList == undefined);
    if (matchResultValue[name].changeList == undefined) {
      getFixedBonusV1(matchResultValue[name].matchId, name);
    }
  }
}
const getMatchResultV1 = async () => {
  try {
    cycle.value++
    const response = await axios.get('https://webapi.sporttery.cn/gateway/jc/football/getMatchResultV1.qry?matchPage=1&matchBeginDate=' + val1.value + '&matchEndDate=' + val2.value + '&leagueId=&pageSize=30&pageNo=' + cycle.value + '&isFix=0&pcOrWap=1');
    let data = response.data;
    if (data.success) {
      matchResult.value = matchResult.value.concat(data.value.matchResult);
      tabsValue.value = data.value.pages;
    } else {
      timeNotify(data.errorMessage);
    }
  } catch (error) {
    // 处理错误
    console.error(error);
  }
}

const getFixedBonusV1 = async (matchId, index) => {
  try {
    const response = await axios.get('https://webapi.sporttery.cn/gateway/jc/football/getFixedBonusV1.qry?clientCode=3001&matchId=' + matchId);
    let data = response.data;
    const responseRate = await axios.get('https://webapi.sporttery.cn/gateway/jc/common/getSupportRateV1.qry?matchIds=' + matchId + '&poolCode=hhad,had&sportType=1');
    const dataRate = responseRate.data;
    const value = dataRate.value;
    let changeList = [];
    let had = data.value.oddsHistory.hadList.length == 0 ? {} : data.value.oddsHistory.hadList[data.value.oddsHistory.hadList.length - 1];
    const isEmpty = Object.keys(had).length === 0;
    let hhad = data.value.oddsHistory.hhadList[data.value.oddsHistory.hhadList.length - 1];
    let hadRate = isEmpty ? {} : value['_' + matchId].HAD;
    let hhadRate = value['_' + matchId].HHAD;
    let winY = 0;
    let drawY = 0;
    let loseY = 0;
    if (!isEmpty) {
      let num1 = parseInt(hadRate.hSupportRate.replace('%', '')) - parseInt(hadRate.hProbability.replace('%', ''));
      let num2 = parseInt(hadRate.dSupportRate.replace('%', '')) - parseInt(hadRate.dProbability.replace('%', ''));
      let num3 = parseInt(hadRate.aSupportRate.replace('%', '')) - parseInt(hadRate.aProbability.replace('%', ''));
      changeList.push({ name: "支持量比例", value1: hadRate.hSupportRate, value2: hadRate.dSupportRate, value3: hadRate.aSupportRate, col: 1, type: 2, num1: num1, num2: num2, num3: num3 });
      changeList.push({ name: "赔率", value1: had.h, value2: had.d, value3: had.a, col: 1, type: 0 });
      changeList.push({ name: "赔率胜率", value1: hadRate.hProbability, value2: hadRate.dProbability, value3: hadRate.aProbability, col: 1, type: 0 });
      changeList.push({ name: "支持量", value1: hadRate.win, value2: hadRate.draw, value3: hadRate.lose, col: 1, type: 0 });
      const hadTotal = hadRate.win + hadRate.draw + hadRate.lose
      let winM = count(hadRate.win, had.h, '*')
      let drawM = count(hadRate.draw, had.d, '*')
      let loseM = count(hadRate.lose, had.a, '*')
      winY = count(hadTotal, winM, '-')
      drawY = count(hadTotal, drawM, '-')
      loseY = count(hadTotal, loseM, '-')
      changeList.push({ name: "总奖池", value1: hadTotal, value2: hadTotal, value3: hadTotal, col: 3, type: 0 });
      changeList.push({ name: "奖池赔付", value1: winM, value2: drawM, value3: loseM, col: 1, type: 0 });
      changeList.push({ name: "庄家盈亏", value1: winY, value2: drawY, value3: loseY, col: 1, type: 1 });
    }
    let hnum1 = parseInt(hhadRate.hSupportRate.replace('%', '')) - parseInt(hhadRate.hProbability.replace('%', ''));
    let hnum2 = parseInt(hhadRate.dSupportRate.replace('%', '')) - parseInt(hhadRate.dProbability.replace('%', ''));
    let hnum3 = parseInt(hhadRate.aSupportRate.replace('%', '')) - parseInt(hhadRate.aProbability.replace('%', ''));
    changeList.push({ name: hhad.goalLine + "支持量比例", value1: hhadRate.hSupportRate, value2: hhadRate.dSupportRate, value3: hhadRate.aSupportRate, col: 1, type: 2, num1: hnum1, num2: hnum2, num3: hnum3 });
    changeList.push({ name: "赔率", value1: hhad.h, value2: hhad.d, value3: hhad.a, col: 1, type: 0 });
    changeList.push({ name: "赔率胜率", value1: hhadRate.hProbability, value2: hhadRate.dProbability, value3: hhadRate.aProbability, col: 1, type: 2 });
    changeList.push({ name: "支持量", value1: hhadRate.win, value2: hhadRate.draw, value3: hhadRate.lose, col: 1, type: 0 });
    const hhadTotal = hhadRate.win + hhadRate.draw + hhadRate.lose
    let hwinM = count(hhadRate.win, hhad.h, '*')
    let hdrawM = count(hhadRate.draw, hhad.d, '*')
    let hloseM = count(hhadRate.lose, hhad.a, '*')
    let hwinY = count(hhadTotal, hwinM, '-')
    let hdrawY = count(hhadTotal, hdrawM, '-')
    let hloseY = count(hhadTotal, hloseM, '-')
    changeList.push({ name: "总奖池", value1: hhadTotal, value2: hhadTotal, value3: hhadTotal, col: 3, type: 0 });
    changeList.push({ name: "奖池赔付", value1: hwinM, value2: hdrawM, value3: hloseM, col: 1, type: 0 });
    changeList.push({ name: "庄家盈亏", value1: hwinY, value2: hdrawY, value3: hloseY, col: 1, type: 1 });
    let winTable = [];
    let loseTbale = [];
    if (!isEmpty) {
      let goalLineValue = parseInt(hhadRate.goalLineValue);
      if (goalLineValue > 0) {
        if ((winY + hwinY) > 0) {
          winTable.push('胜+让胜:' + count(winY, hwinY, '+'));
        } else if ((winY + hwinY) < 0) {
          loseTbale.push('胜+让胜:' + count(winY, hwinY, '+'));
        }
        if ((drawY + hwinY) > 0) {
          winTable.push('平+让胜:' + count(drawY, hwinY, '+'));
        } else if ((drawY + hwinY) < 0) {
          loseTbale.push('平+让胜:' + count(drawY, hwinY, '+'));
        }
        if ((loseY + hdrawY) > 0) {
          winTable.push('负+让平:' + count(loseY, hdrawY, '+'));
        } else if ((loseY + hdrawY) < 0) {
          loseTbale.push('负+让平:' + count(loseY, hdrawY, '+'));
        }
        if ((loseY + hloseY) > 0) {
          winTable.push('负+让负:' + count(loseY, hloseY, '+'));
        } else if ((loseY + hloseY) < 0) {
          loseTbale.push('负+让负:' + count(loseY, hloseY, '+'));
        }
      } else {
        if ((winY + hwinY) > 0) {
          winTable.push('胜+让胜:' + count(winY, hwinY, '+'));
        } else if ((winY + hwinY) < 0) {
          loseTbale.push('胜+让胜:' + count(winY, hwinY, '+'));
        }
        if ((winY + hdrawY) > 0) {
          winTable.push('胜+让平:' + count(winY, hdrawY, '+'));
        } else if ((winY + hdrawY) < 0) {
          loseTbale.push('胜+让平:' + count(winY, hdrawY, '+'));
        }
        if ((drawY + hloseY) > 0) {
          winTable.push('平+让负:' + count(drawY, hloseY, '+'));
        } else if ((drawY + hloseY) < 0) {
          loseTbale.push('平+让负:' + count(drawY, hloseY, '+'));
        }
        if ((loseY + hloseY) > 0) {
          winTable.push('负+让负:' + count(loseY, hloseY, '+'));
        } else if ((loseY + hloseY) < 0) {
          loseTbale.push('负+让负:' + count(loseY, hloseY, '+'));
        }
      }
    } else {
      if (hwinY > 0) {
        winTable.push('让胜:' + hwinY);
      } else if (hwinY < 0) {
        loseTbale.push('让胜:' + hwinY);
      }
      if (hdrawY > 0) {
        winTable.push('让平:' + hdrawY);
      } else if (hdrawY < 0) {
        loseTbale.push('让平:' + hdrawY);
      }
      if (hloseY > 0) {
        winTable.push('让负:' + hloseY);
      } else if (hloseY < 0) {
        loseTbale.push('让负:' + hloseY);
      }
    }
    let matchResultValue = matchResult.value;
    matchResultValue[index].changeList = changeList;
    matchResultValue[index].winTable = winTable;
    matchResultValue[index].loseTbale = loseTbale;
    matchResult.value = matchResultValue;
    console.log(matchResult.value);
  } catch (error) {
    // 处理错误
    console.error(error);
  }
}
const count = (num1, num2, symbol) => {
  let str1 = num1.toString();
  let str2 = num2.toString();
  let result,
    len1,
    len2,
    step;
  try {
    len1 = str1.split('.')[1].length
  } catch (e) {
    len1 = 0
  };
  try {
    len2 = str2.split('.')[1].length
  } catch (e) {
    len2 = 0
  };
  step = Math.pow(10, Math.max(len1, len2));
  let a = parseInt(num1 * step);
  let b = parseInt(num2 * step);
  switch (symbol) {
    case '+':
      result = (a + b) / step
      break;
    case "-":
      result = (a - b) / step
      break;
    case "*":
      result = (a * b) / step / step
      break;
    case "/":
      result = a / b
      break;
    default:
      break;
  }
  return result;
}

// 获取当前日期并格式化
const formattedCurrentDate = () => {
  const date = new Date();
  const year = date.getFullYear();
  const month = String(date.getMonth() + 1).padStart(2, '0');
  const day = String(date.getDate()).padStart(2, '0');
  return new Date(year, month, day);
};

// 获取当前日期并格式化
const formattedCurrentDateString = () => {
  const date = new Date();
  const year = date.getFullYear();
  const month = String(date.getMonth() + 1).padStart(2, '0');
  const day = String(date.getDate()).padStart(2, '0');
  return `${year}-${month}-${day}`;
};

// 获取3天前的日期并格式化
const formattedThreeDaysAgo = () => {
  const date = new Date();
  date.setDate(date.getDate() - 3);
  const year = date.getFullYear();
  const month = String(date.getMonth() + 1).padStart(2, '0');
  const day = String(date.getDate()).padStart(2, '0');
  return new Date(year, month, day);
};

// 获取3天前的日期并格式化
const formattedThreeDaysAgoString = () => {
  const date = new Date();
  date.setDate(date.getDate() - 3);
  const year = date.getFullYear();
  const month = String(date.getMonth() + 1).padStart(2, '0');
  const day = String(date.getDate()).padStart(2, '0');
  return `${year}-${month}-${day}`;
};

onMounted(refreshFun);
</script>

<style>
.contentF {
  line-height: 40px;
  font-size: 12px;
  text-align: center;
  border: 1px solid var(--nut-table-border-color, #ececec);
  vertical-align: middle;
}
.danguan {
  background-image: url(https://static.sporttery.cn/res_1_0/jcw/images/jc_schedule/single.gif);
  background-repeat: no-repeat;
  padding-left: 20px;
  margin-right: 20px;
}
</style>