<template>
  <nut-noticebar :text="text" />
  <nut-pull-refresh v-model="refresh"
                    loosing-txt="松开吧"
                    loading-txt="玩命加载中..."
                    :complete-duration="1000"
                    @refresh="refreshFun">
    <template #pulling-txt>
      <div>用力拉</div>
    </template>
    <div v-for="(item, index) in matchTempList"
         :key="index">
      <nut-divider content-position="left"> {{ item.startSellDate }} {{ item.weekDate }} 共{{item.matchCount}}场比赛 </nut-divider>
      <nut-row v-for="(match, indexMatch) in item.matchList"
               :key="indexMatch">
        <nut-col :span="8">
          <div class="contentF">{{match.matchDatetime}}</div>
        </nut-col>
        <nut-col :span="6">
          <div class="contentF">{{match.leagueName}}</div>
        </nut-col>
        <nut-col :span="10">
          <div class="contentF">{{match.homeCn}} VS {{match.awayCn}}</div>
        </nut-col>
      </nut-row>
    </div>
  </nut-pull-refresh>
</template>

<script setup>
import { onMounted, ref } from 'vue'
import axios from 'axios'
const matchTempList = ref([]);
const refresh = ref(true)
const text = ref(
  '当前页面为体彩未来赛事预览。'
)
const refreshFun = () => {
  getFbMatchTempV1();
}
const getFbMatchTempV1 = async () => {
  try {
    const response = await axios.get('https://webapi.sporttery.cn/gateway/jc/football/getFbMatchTempV1.qry');
    let data = response.data;
    console.log(data);
    matchTempList.value = data.value.matchTempList;
    refresh.value = false
  } catch (error) {
    // 处理错误
    console.error(error);
  }
}
onMounted(refreshFun);
</script>

<style>
.contentF {
  line-height: 40px;
  font-size: 12px;
  text-align: center;
  border: 1px solid var(--nut-table-border-color, #ececec);
  vertical-align: middle;
}
</style>